:local .form-group label {
  font-weight: 500;
  color: #617083;
  transition: color 0.15s ease-in-out;
  font-size: 1rem; }

:local .form-group input::placeholder {
  color: #7f92a9; }

:local .check-box-container input[type=checkbox] + label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  display: inline-block;
  height: 1.5625rem;
  line-height: 1.5625rem;
  user-select: none;
  margin-left: 5px; }

:local .check-box-container input[type=checkbox] + label:before {
  content: '';
  position: absolute;
  transition: border .25s, background-color .25s, width .2s .1s, height .2s .1s, top .2s .1s, left .2s .1s;
  z-index: 1; }

:local .check-box-container input[type=checkbox] + label:after {
  content: '';
  left: 0;
  position: absolute;
  transition: border .25s, background-color .25s, width .2s .1s, height .2s .1s, top .2s .1s, left .2s .1s;
  z-index: 1; }

:local .check-box-container input[type=checkbox]:not(:checked) + label:before {
  width: 0;
  height: 0;
  border: 3px solid transparent;
  left: 6px;
  top: 10px;
  transform: rotateZ(37deg);
  transform-origin: 100% 100%; }

:local .check-box-container input[type=checkbox]:not(:checked) + label:after {
  height: 20px;
  width: 20px;
  background-color: transparent;
  border: 2px solid #5a5a5a;
  top: 0; }

:local .check-box-container input[type=checkbox]:checked + label:after {
  top: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #3c9bfd;
  background-color: #3c9bfd;
  z-index: 0; }

:local .check-box-container input[type=checkbox]:checked + label:before {
  top: 2px;
  left: 2px;
  width: 7px;
  height: 13px;
  border-radius: 4px;
  border: 2px solid transparent;
  border-right-color: #fff;
  border-bottom-color: #fff;
  transform: rotateZ(37deg);
  transform-origin: 100% 100%; }

:local .footerLi {
  color: #a6b8d2; }
  :local .footerLi:hover {
    color: #3c495a; }
