:local .top {
  font-family: 'Raleway', sans-serif !important;
  background-color: #086080; }

:local .underImg {
  position: relative;
  height: 260px;
  margin-top: -1px;
  background-color: #086080; }

:local .imgTop {
  height: 390px; }

:local .content {
  padding-top: 150px;
  background-color: white; }

@media (max-width: 576px) {
  :local .underImg {
    position: relative;
    height: 125px;
    margin-top: -1px;
    background-color: #086080; }
  :local .imgTop {
    height: 190px; }
  :local .content {
    padding-top: 90px;
    background-color: white; } }

@media (max-width: 330px) {
  :local .underImg {
    position: relative;
    height: 105px;
    margin-top: -1px;
    background-color: #086080; }
  :local .imgTop {
    height: 160px; } }
