#jobContainer {
  display: flex;
  height: 100%;
  flex-direction: column;
  color: #2b343f;
  font-weight: 400;
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -webkit-box-direction: normal;
  -webkit-tap-highlight-color: transparent;
  word-break: break-word;
  padding-top: 58px; }
  @media (max-width: 767.98px) {
    #jobContainer {
      padding-top: 0; } }
  #jobContainer a {
    color: inherit;
    outline: none;
    text-decoration: none !important; }
    #jobContainer a:hover {
      color: inherit; }
  #jobContainer h1,
  #jobContainer h2,
  #jobContainer h3,
  #jobContainer h4,
  #jobContainer h5,
  #jobContainer h6 {
    font-weight: inherit; }
  #jobContainer .height {
    height: 100%; }
  #jobContainer .width {
    width: 100%; }
  #jobContainer .img-fluid {
    width: 100%;
    height: auto; }
  #jobContainer .img-fluid-height {
    height: 100%;
    width: auto; }
  #jobContainer .border-radius-20 {
    border-radius: 20px !important; }
  #jobContainer .main-color {
    color: #55a8fd; }
  #jobContainer a.main-color:hover {
    color: #3c9bfd; }
  #jobContainer .main-color-hover:hover {
    color: #55a8fd; }
  #jobContainer .page {
    box-shadow: 0 2px 4px rgba(0, 0, 1, 0.15);
    background: white;
    border-radius: 0.25rem;
    overflow: hidden; }
  #jobContainer .cubic-bez {
    transition: all 0.3s cubic-bezier(0.57, 0.21, 0.69, 1.25); }
  #jobContainer .ease-in-out {
    transition: all 0.3s ease-in-out; }
  #jobContainer .blur {
    color: #7f92a9; }
  #jobContainer .text-gray {
    color: #617083; }
  #jobContainer .btn-primary {
    background-color: #55a8fd !important;
    font-weight: 500;
    border-radius: 0.25rem; }
    #jobContainer .btn-primary:hover {
      background-color: #3c9bfd !important; }
  #jobContainer .btn-warning {
    background-color: #FFA04D !important;
    font-weight: 500; }
    #jobContainer .btn-warning:hover {
      background-color: #ffae67 !important; }
  #jobContainer .btn {
    padding: 10px 14px;
    border-radius: 0.25rem; }
  #jobContainer .btn-success {
    background: #56d48f !important;
    font-weight: 500;
    border: none;
    color: white;
    text-transform: capitalize; }
    #jobContainer .btn-success:hover {
      background: #6ad99c; }
  #jobContainer .text-green {
    color: #56d48f !important; }
  #jobContainer .primary-text {
    color: #2b343f !important; }
  #jobContainer .trun-2-line {
    -webkit-box-orient: vertical;
    overflow: hidden;
    display: -webkit-box !important;
    -webkit-line-clamp: 2; }
  #jobContainer .item-skill {
    display: inline-block;
    border: 1px solid #ced9ee;
    border-radius: 25px;
    padding: 6px 12px 5px;
    cursor: default;
    font-size: 0.85rem;
    transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms; }
    #jobContainer .item-skill:hover {
      background-color: #55a8fd;
      box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
      border-color: #55a8fd;
      color: white; }
  #jobContainer .font-weight-500 {
    font-weight: 500; }
  #jobContainer .slick-slider {
    user-select: text; }
  #jobContainer .slick-active .home-slide-paging {
    background: white !important; }
  #jobContainer .btn-transparent {
    border: 1px solid white !important;
    color: white !important;
    padding: 12px 30px !important;
    text-transform: capitalize;
    font-weight: 500;
    transition: all 0.3s ease-in-out;
    box-shadow: none !important;
    border-radius: 0.25rem; }
    #jobContainer .btn-transparent:hover {
      background: white !important;
      color: #55a8fd !important; }
  #jobContainer .logo-title {
    color: #55a8fd;
    font-size: 1.75rem;
    font-family: Journey, serif;
    text-transform: uppercase; }
  #jobContainer .font-size-14 {
    font-size: 14px; }
  #jobContainer input[type=checkbox] {
    opacity: 0;
    pointer-events: none; }
  #jobContainer textarea {
    resize: none; }
  #jobContainer .bottom-border-blur {
    border-bottom: 1px solid #ced9ee; }
  #jobContainer .form-group .error {
    color: #dc3545; }
  #jobContainer .form-group label {
    font-weight: 500;
    color: #617083;
    transition: color 0.15s ease-in-out;
    font-size: 1rem;
    text-transform: capitalize; }
  #jobContainer .form-group input::placeholder, #jobContainer .form-group textarea::placeholder {
    color: #7f92a9; }
  @media (max-width: 991.98px) {
    #jobContainer .container {
      max-width: 100%; } }
