:local .toolTip {
  position: absolute;
  top: -40%;
  left: 70px;
  margin: auto;
  background-color: white;
  color: white;
  white-space: nowrap;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.3);
  border-radius: 2px; }

:local .children {
  color: #777777;
  cursor: pointer; }
  :local .children:hover {
    color: #2980b9; }

:local .triangleLeft {
  position: absolute;
  top: 30%;
  bottom: 0;
  left: -10px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-right: 10px solid white;
  border-bottom: 7px solid transparent; }

:local .active {
  color: #2980b9 !important; }
