:local .header {
  background-color: #0b3648;
  width: 100%;
  position: fixed;
  z-index: 100;
  top: 0; }

:local .navItemHover1 {
  background-color: #dddddd;
  color: black !important; }

:local .navSub {
  position: absolute;
  width: 270px;
  color: black;
  white-space: nowrap;
  z-index: 1000; }

:local .navItem {
  border-bottom: 1px solid;
  border-bottom: 1px solid #ccc;
  background-color: white; }
  :local .navItem:hover {
    background-color: #3e90ca;
    color: white; }

:local .navItemHover2 {
  background-color: #3e90ca;
  color: white !important; }

:local .navSubSub {
  position: absolute;
  width: 350px;
  background-color: #3e90ca;
  top: 0;
  left: 270px;
  right: 0;
  z-index: 100;
  height: 100%; }

:local .navTitle {
  background-color: #1a6283; }

:local .navLinkUnderline:hover {
  text-decoration: underline !important; }

:local .navLink {
  color: #bdc3c7;
  text-decoration: none !important; }
  :local .navLink:hover {
    color: white; }
