:local .loginContainer {
  max-width: 400px;
  margin: 0 55px; }
  @media (max-width: 991.98px) {
    :local .loginContainer {
      margin: auto; } }

:local .form-group label {
  font-weight: 500;
  color: #617083;
  transition: color 0.15s ease-in-out; }

:local .form-group:focus-within label {
  color: #F68826; }

:local .form-group input {
  border: 2px solid #E5E5E5; }
  :local .form-group input:focus {
    border-color: #F68826;
    box-shadow: none; }
  :local .form-group input::placeholder {
    color: #7f92a9; }

:local .bg {
  background: url(./bg_white.svg) no-repeat -350px 0, linear-gradient(135deg, #1f95ff 0%, #0068FF 100%);
  position: relative; }

:local .object {
  width: 65%;
  height: auto;
  position: absolute;
  right: 5px;
  bottom: 20px; }

:local .btn {
  border: 2px solid white !important;
  background: transparent !important;
  border-radius: .25rem;
  box-shadow: none !important;
  color: white;
  margin: 1rem 0 !important;
  padding: 0.5rem 2rem; }
  :local .btn:hover {
    background: white !important;
    color: #55a8fd !important; }
