:local .dropdownItem {
  font-weight: 400 !important;
  color: #617083 !important;
  padding: 12px 12px;
  font-size: 14px; }
  :local .dropdownItem:active, :local .dropdownItem:hover {
    background-color: #f3f6fb !important;
    color: #262626 !important; }

:local .dropdown {
  min-width: 9rem; }

:local .item {
  padding: 0 12px !important;
  height: 100%;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 500;
  margin: auto 4px; }
  :local .item div {
    background: #55a8fd !important; }
  :local .item:hover {
    background-color: #f3f6fb; }

:local .root {
  width: 100%;
  flex-grow: 1;
  height: 58px;
  background-color: white !important;
  position: fixed;
  z-index: 1000;
  top: 0;
  box-shadow: 0 2px 4px rgba(0, 0, 1, 0.2);
  padding: 0; }

:local .phoneCol {
  background: white;
  right: 0;
  left: 0;
  position: fixed;
  top: 58px; }

:local .active {
  color: #55a8fd !important;
  background-color: #f3f6fb !important; }
