:local .container {
  display: flex;
  background-color: transparent;
  padding: 0;
  text-align: center;
  margin: 1em auto;
  border-radius: 0.25em;
  list-style: none; }
  :local .container li {
    position: relative;
    float: none;
    display: inline-block;
    margin: 10px 40px 10px 10px;
    cursor: default; }
    :local .container li.visited div {
      color: white !important;
      background-color: #96c03d !important; }
    :local .container li.visited::after {
      background-color: #96c03d; }
    :local .container li.current::after {
      background-color: #ccc !important; }
    :local .container li::after {
      width: 100%;
      top: 50%;
      left: 100%;
      transform: translateY(-50%) translateX(-1px);
      position: absolute;
      content: "";
      height: 4px;
      margin: 0;
      background-color: #ccc; }
    :local .container li div {
      box-shadow: 0 2px 4px rgba(0, 0, 1, 0.15);
      color: #2c3f4c;
      background: white;
      z-index: 1;
      padding: 8px 12px;
      border-radius: 0.25em;
      position: relative; }
    :local .container li i {
      margin-right: 8px; }
    :local .container li:last-child {
      margin-right: 0; }
      :local .container li:last-child::after {
        background-color: transparent;
        height: 0; }
