:local .searchContainer {
  border-radius: 0.25rem;
  box-shadow: 0 2px 4px rgba(0, 0, 1, 0.15); }
  :local .searchContainer input {
    border-radius: 0.25rem 0 0 0.25rem;
    background-color: white;
    font-size: 16px;
    padding: 13px 20px;
    border: 0;
    width: 100%;
    margin: 1px 0;
    transition: all 0.2s ease-in-out; }
    :local .searchContainer input:focus {
      outline: 0;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(85, 168, 253, 0.6); }
  :local .searchContainer button {
    border-radius: 0 0.25rem 0.25rem 0 !important;
    box-shadow: none !important; }

